import "./QuickBooks.css"
import { Component, For, Show, createMemo } from "solid-js"
import Book from "../../common/book/Book"
import state from "../../../state/state"
import { getBooksData } from "../../../actions/getBooksData"

const QuickBooks: Component = () => {
  const booksInProgress = createMemo(() => {
    return getBooksData(
      state.get.booksIndex
        .filter((book) => book.progress > 0 && book.progress < 1)
        .sort((a, b) => b.timestamp - a.timestamp),
    )
  })

  return (
    <Show when={booksInProgress().length > 0}>
      <section class="quick-books">
        <h2>In Progress</h2>
        <For each={booksInProgress().slice(0, 3)}>
          {(book) => (
            <Book
              id={book.id}
              coverUrl={book.coverUrl}
              title={book.title}
              author={book.author}
              description={book.description}
              pages={book.pages}
              progress={book.progress}
              favorite={book.favorite}
              isNew={book.isNew}
              genres={book.genres}
            />
          )}
        </For>
        <Show when={booksInProgress().length > 3}>
          <footer>
            <a class="button primary" href="/books?tab=in-progress">
              Show All in Progress
            </a>
          </footer>
        </Show>
      </section>
    </Show>
  )
}

export default QuickBooks
